.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: 10;
  background: rgba(0, 0, 0, 0.75);
}
.modal {
  animation: modal 0.6s ease-in-out 1;
  position: fixed;
  top: 30vh;
  left: 10%;
  width: 80%;
  z-index: 100;
  overflow: hidden;
}
.header {
  background: #F54100;
  padding: 1rem;
}
.header h2 {
  margin: 0;
  color: white;
}
.content {
  padding: 1rem;
}
.actions {
  padding: 1rem;
  display: flex;
  justify-content: flex-end;
}
@media (min-width: 768px) {
  .modal {
    left: calc(50% - 20rem);
    width: 40rem;
  }
}

@keyframes modal {
  0% {
    transform: translateY(-350px);
  }
  100% {
    transform: translateY(0px);
  }
}